<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <div>
                    <h2>Вход в систему</h2>
                    <form>
                        <v-text-field
                                v-model="email"
                                label="E-mail"
                                required
                        ></v-text-field>
                        <v-text-field
                                v-model="password"
                                label="Password"
                                type="password"
                                required
                        ></v-text-field>

                        <v-btn :disabled="!password || !email" class="mr-4 primary" @click="submit">Войти
                        </v-btn>
                    </form>
                </div>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        name: 'Login',

        data: () => ({
            password: '',
            email: ''
        }),

        methods: {
            ...mapActions(['login']),
            submit() {
                this.login({email: this.email, password: this.password}).then(() => {
                    this.$router.push('/accounts')
                })

            }
        },
    }
</script>

